body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.SizeInput {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: justify;
      justify-content: space-between;
  -ms-flex-align: center;
      align-items: center;

  height: 2.5rem;

  font-family: monospace;
}
.SizeInput-label {

}
.SizeInput-field {
  width: 5rem;
  margin-right: 0.5rem;

  text-align: right;

  border: 0;
  border-bottom: 1px solid #dadada;
}
.SelectorGroup {
  position: relative;

  display: grid;

  padding: 15px;
  border: 1px solid #dadada;

  background-color: white;
}
.SelectorGroup-selector {
  display: inline-block;
  margin-right: 5px;
}
.SelectorGroup-input {
  padding-left: 20px;
}
.SelectorGroup-remove-selector {
  position: absolute;
  top: 0;
  right: 0;
  -webkit-transform: translate(30%, -30%);
          transform: translate(30%, -30%);

  display: -ms-flexbox;

  display: flex;
  -ms-flex-pack: center;
      justify-content: center;
  -ms-flex-align: center;
      align-items: center;

  height: 20px;
  width: 20px;
  border-radius: 100%;
  border: none;

  color: white;
  font-weight: bolder;

  background-color: #eb5d52;

  cursor: pointer;
}
.SelectorGroup-remove-selector-hover {
  -webkit-animation: shake 850ms cubic-bezier(.36,.07,.19,.97) both;
          animation: shake 850ms cubic-bezier(.36,.07,.19,.97) both;
  -webkit-transform: translate3d(0, 0, 0);
          transform: translate3d(0, 0, 0);
  -webkit-backface-visibility: hidden;
          backface-visibility: hidden;
  -webkit-perspective: 1000px;
          perspective: 1000px;
}

@-webkit-keyframes shake {
  10%, 90% {
    -webkit-transform: translate3d(-1px, 0, 0);
            transform: translate3d(-1px, 0, 0);
  }

  20%, 80% {
    -webkit-transform: translate3d(1px, 0, 0);
            transform: translate3d(1px, 0, 0);
  }

  30%, 50%, 70% {
    -webkit-transform: translate3d(-2px, 0, 0);
            transform: translate3d(-2px, 0, 0);
  }

  40%, 60% {
    -webkit-transform: translate3d(2px, 0, 0);
            transform: translate3d(2px, 0, 0);
  }
}

@keyframes shake {
  10%, 90% {
    -webkit-transform: translate3d(-1px, 0, 0);
            transform: translate3d(-1px, 0, 0);
  }

  20%, 80% {
    -webkit-transform: translate3d(1px, 0, 0);
            transform: translate3d(1px, 0, 0);
  }

  30%, 50%, 70% {
    -webkit-transform: translate3d(-2px, 0, 0);
            transform: translate3d(-2px, 0, 0);
  }

  40%, 60% {
    -webkit-transform: translate3d(2px, 0, 0);
            transform: translate3d(2px, 0, 0);
  }
}
.AddSelector-form {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: center;
      justify-content: center;

  width: 100%;
}
.AddSelector-input,
.AddSelector-submit {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-align: center;
      align-items: center;

  height: 40px;
  line-height: 40px;
  padding: 10px;
}
.AddSelector-input {
  width: 25%;
  border: 1px solid #dadada;
  border-radius: 3px 0 0 3px;
  border-right-width: 0;

  font-size: 20px;
}
.AddSelector-submit {
  padding: 0 15px;
  background: #33be7b;
  border: 0 none;
  border-radius: 0 3px 3px 0;

  color: white;
  font-size: 25px;

  cursor: pointer;
}
.AddSelector-submit[disabled] {
  opacity: 0.5;
}
.CSSDisplay {
  width: 90%;
  margin: 50px auto 100px;
}
.CSSDisplay-key {
  text-align: center;
}
*,
*:before,
*:after {
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
}
body {
